import React from "react";

import { useLocation, Link } from "react-router-dom"
import clsx from "clsx"
export default ((props) => {
    // const { match, location, history } = props;
    const location = useLocation();
    let reversed = false;
    switch (location.pathname) {
        case "/":
            reversed = true; break;
        case "/ourwork/leagues_media":
            reversed = true; break;
        default:
            break;
    }
    let reversed_top_padding = false;
    switch (location.pathname) {
        case "/ourwork/leagues_media":
            reversed_top_padding = true; break;
        default:
            break;
    }
    return (
        <div className={clsx("footer-wrapper", reversed ? "reversed" : null)}>
            <div className={clsx("container footer footer-no-cta", reversed ? "reversed" : null, reversed_top_padding ? "cta_top_padding" : null)}>
                <div className={clsx("footer__contact-title", reversed ? "reversed" : null, "reg-heading", reversed ? "reversed" : null)}>Contact Us</div>
                <div className="footer__contact">
                    <a href="https://www.google.com/maps/place/312+E.+1st+St.,+5th+Fl.,+Los+Angeles,+CA+90012/@34.0502681,-118.2407809,16z/data=!4m2!3m1!1s0x0:0xb8f4c0e6602dc6cb" target="_blank" className={clsx("footer__link reg-caption", reversed ? "reversed" : null)}>312 East 1st Street, 5th Floor  Los Angeles, CA 90012 USA</a>
                    <a href="mailto:info@secondspectrum.com" className={clsx("footer__link reg-caption", reversed ? "reversed" : null)}>info@secondspectrum.com</a>
                    <a href="tel:213-995-6860" className={clsx("footer__link reg-caption", reversed ? "reversed" : null)}>213.995.6860</a>
                </div>
                <div className={clsx("footer__nav-title", reversed ? "reversed" : null, "reg-heading", reversed ? "reversed" : null)}>Company</div>
                <div className="footer__nav">
                    <a href="/careers/opportunities.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Opportunities</a>
                    <a href="/careers/culture.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Culture</a>
                    <a href="/ourwork/teams-leagues.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Teams &amp; Leagues</a>
                    <a href="/ourwork/leagues-media.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Leagues &amp; Media</a>
                    <a href="/contact.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Contact</a>
                    <a href="/press/index.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Press</a>
                    <a href="/video.html" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Videos</a>
                    <a href="/login" className={clsx("dns-subheading", reversed ? "reversed" : null)}>Log In</a>
                </div>
                <div className="footer__logo">
                    {/* <img src="" alt=""> */}
                </div>
                <div className="footer__social">
                    <div className="footer__button">
                        <a href="#a">이미지</a>
                    </div>
                    <div className="footer__button">
                        <a href="#a">이미지</a>
                    </div>
                    <div className="footer__button">
                        <a href="#a">이미지</a>
                    </div>
                    <div className="footer__button">
                        <a href="#a">이미지</a>
                    </div>
                </div>
                <div className="footer__privacy_policy">
                    Copyright © 2018 - 2020 Dreamshake, All Rights Reserved. Read our
                <a href="/privacypolicy.html">Privacy Policy</a>
                </div>
            </div>
        </div>
    )
})