import React, { Component } from 'react';

class Home extends Component {
  render() {
    return (
      <div className="home-page">
        <div className="section section-split home-video">
          <div className="container section-content">
            <h1 className="page-title page-title-reversed">The Next Way of Seeing Sports</h1>
            <div data-remodal-target="modal" className="btn-wrapper">
              <a href="https://player.vimeo.com/video/361930804" className="btn-blue btn-play btn-play-reversed">
                <span>Click to Watch</span>
              </a>
            </div>
          </div>
        </div>
        <div className="section section-split home-teams">
          <div className="section-split-content">
            <h3 className="section-title section-title-reversed">Unlock the Future</h3>
            <p className="section-text section-text-reversed">
              Second Spectrum is the Official Optical Tracking Provider for both the NBA and
              the Premier League and provides an unparalleled machine understanding of the game.  In partnership with these leagues,
              Second Spectrum has created never before seen products and experiences for coaches, analysts, players, producers,
              storytellers, and fans everywhere.
                </p>
            <div className="btn-wrapper">
              <a href="/ourwork/teams-leagues.html" className="btn btn-red">
                <span>Learn More</span>
              </a>
            </div>
          </div>
          <div className="section-split-image"></div>
        </div>
        <div className="section section-split home-leagues">
          <div className="section-split-content">
            <h2 className="section-header section-header-reversed">For Leagues &amp; Media</h2>
            <h3 className="section-title section-title-reversed">Engage Viewers</h3>
            <p className="section-text section-text-reversed">
              Create personalized, interactive, augmented content and experiences powered by machine understanding of sports.
                </p>
            <div className="btn-wrapper">
              <a href="/ourwork/leagues-media.html" className="btn btn-pink">
                <span>Learn More</span>
              </a>
            </div>
            {/* <img src="/assets/images/00Homepage/02EngageViewers/Logos.svg"> */}
          </div>
          <div className="section-split-image"></div>
        </div>
        <div className="section section-split home-win">
          <div className="section-split-content">
            <h3 className="section-header">For Teams</h3>
            <h2 className="section-title">Win More Games</h2>
            <p className="section-text">
              Successful teams rely on our machine intelligence to find and share video, discover key insights,
              and be more informed in smarter, better and faster ways.
                </p>
            <div className="btn-wrapper">
              <a href="/ourwork/teams-leagues.html" className="btn btn-green">
                <span>Learn More</span>
              </a>
            </div>
            {/* <img src="/assets/images/00Homepage/03WinMoreGames/Logos.svg"> */}
          </div>
          <div className="section-split-image"></div>
        </div>
        <div className="section home-cta">
          <div className="container section-content">
            <div className="section-split-text">
              <h3 className="section-header section-header-reversed">Join Us</h3>
              <h2 className="section-title section-title-reversed">We're excited to meet you!</h2>
            </div>
            <div className="home-cta-buttons">
              <div>
                <a href="/careers/culture.html" className="btn btn-grey">
                  <span className="index-cta__button-text">Culture</span>
                </a>
              </div>
              <div>
                <a href="/careers/opportunities.html" className="btn btn-orange">
                  <span className="index-cta__button-text">Opportunities</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
