import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import './App.scss';
import Home from "./Pages/Home";
import Opportunities from "./Pages/careers/Opportunities";
import Culture from "./Pages/careers/Culture";
import Teams_leagues from "./Pages/ourwork/Teams_leagues";
import Leagues_media from "./Pages/ourwork/Leagues_media";
import Contact from "./Pages/Contact";
import Nav from './Nav';
import Footer from './Footer';

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          <div className="global-wrapper default">
            {/* 네비게이션 시작*/}
            <Nav/>
            {/* 네비 끝 */}
            {/* 바디 시작 */}
            <Switch>
              <Route exact path="/">
                <Home />
              </Route>
              <Route path="/careers/opportunities">
                <Opportunities />
              </Route>
              <Route path="/careers/culture">
                <Culture />
              </Route>
              <Route path="/ourwork/teams_leagues">
                <Teams_leagues />
              </Route>
              <Route path="/ourwork/leagues_media">
                <Leagues_media />
              </Route>
              <Route path="/contact">
                <Contact />
              </Route>
            </Switch>
            {/* 바디 끝 */}
            <Footer />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
