import React, { useState } from "react";
import { useLocation, Link } from "react-router-dom"
import clsx from "clsx"

export default ((props) => {
    // const { match, location, history } = props;
    const location = useLocation();
    // const [isToggleon, setToggleOn] = useState(false);
    // const [careerToggleon, setcareerToggleOn] = useState(false);
    // const [ourworkToggleon, setourworkToggleOn] = useState(false);

    const [toggles, setToggles] = useState({
        menu: false,
        career: false,
        work: false
    })
    let reversed = false;
    switch (location.pathname) {
        case "/":
            reversed = true; break;
        case "/ourwork/leagues_media":
            reversed = true; break;
        default:
            break;
    }

    const handleToggle = (e) => {
        const { rel } = e.currentTarget;
        switch (rel) {
            case 'work':
            case 'career':
                setToggles({
                    ...toggles,
                    [rel]: !toggles[rel]
                });
                break;
            default:
                setToggles({
                    ...toggles,
                    menu: !toggles.menu
                });
                break;
        }
    }

    const pageOut = (e) => {
        if(toggles.menu){
            setToggles({
                ...toggles,
                menu: !toggles.menu,
                career: false,
                work: false
            })
        }
    }

    return (
        <div className={clsx("banner-header", reversed ? "reversed" : null)}>
            <div className="header-wrapper">
                <div className="header">
                    <div className="header_logo">
                        <Link to="/" className={clsx("navbar-brand", reversed ? "reversed" : null)}
                        onClick={pageOut}>
                            헤더 이미지 파일
                        </Link>
                    </div>
                    <div className="nav-wrapper">
                        <div className={clsx("nav-button", toggles.menu ? "nav-active nav-mobile-open" : null)}
                            onClick={handleToggle}>
                            <span className={clsx("nav-button__line", reversed ? "reversed" : null)}></span>
                            <span className={clsx("nav-button__line", reversed ? "reversed" : null)}></span>
                        </div>
                        <div className={clsx("nav", reversed ? "reversed" : null)}>
                            <ul>
                                <li>
                                    <a href="#" className={clsx("nav-link has-subnav for-mobile", reversed ? "reversed" : null, toggles.career ? "is-open" : null)}
                                        rel={"career"}
                                        onClick={handleToggle}>Careers</a>
                                    <a href="#" className={clsx("nav-link has-subnav for-desktop", reversed ? "reversed" : null)}>Careers</a>
                                    <div className={clsx("nav-submenu", reversed ? "reversed" : null)}>
                                        <div className="sub-element">
                                            <Link to="/careers/opportunities" className={clsx("nav-sublink", reversed ? "reversed" : null)}
                                            onClick={pageOut}>Opportunities</Link>
                                        </div>
                                        <div className="sub-element">
                                            <Link to="/careers/culture" className={clsx("nav-sublink", reversed ? "reversed" : null)}onClick={pageOut}>Culture</Link>
                                        </div>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" className={clsx("nav-link has-subnav for-mobile", reversed ? "reversed" : null, toggles.work ? "is-open" : null)}
                                        rel={"work"}
                                        onClick={handleToggle}>Our Work</a>
                                    <a href="#" className={clsx("nav-link has-subnav for-desktop", reversed ? "reversed" : null)}>Our Work</a>
                                    <div className={clsx("nav-submenu", reversed ? "reversed" : null)}>
                                        <div className="sub-element">
                                            <Link to="/ourwork/teams_leagues" className={clsx("nav-sublink", reversed ? "reversed" : null)}
                                            onClick={pageOut}>Teams &amp; Leagues</Link>
                                        </div>
                                        <div className="sub-element">
                                            <Link to="/ourwork/leagues_media" className={clsx("nav-sublink", reversed ? "reversed" : null)}
                                            onClick={pageOut}>Leagues &amp; Media</Link>
                                        </div>
                                    </div>
                                </li>
                                <li>
                                    <Link to="/contact" className={clsx("nav-link", reversed ? "reversed" : null)}
                                    onClick={pageOut}>Contact</Link>
                                </li>
                                <li>
                                    <Link to="/press" className={clsx("nav-link", reversed ? "reversed" : null)}
                                    onClick={pageOut}>Press</Link>
                                </li>
                                <li>
                                    <Link to="/video" className={clsx("nav-link", reversed ? "reversed" : null)}
                                    onClick={pageOut}>Videos</Link>
                                </li>
                                <li>
                                    <Link to="/login" className={clsx("nav-link", reversed ? "reversed" : null)}
                                    onClick={pageOut}>Log In</Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
})